@import './blocks/variables.scss';
@import './blocks/mixins.scss';

.page__body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.watch {
  position: relative;
  width: 80vmin;
  height: 80vmin;
  border: 5px dotted black;
  border-radius: 50%;

  &__center {
    width: 8vmin;
    height: 8vmin;
    background-color: orange;
    border-radius: 50%;
    position: absolute;
    left: $watchSize / 2 - $watchCenterSize / 2;
    top: $watchSize / 2 - $watchCenterSize / 2;
  }

  &__hand {
    transform-origin: bottom center;
    position: absolute;

    &-second {
      height: $watchSize / 2;
      width: $secondHandWidth;
      background-color: green;
      left: $watchSize / 2 - $secondHandWidth / 2;
      @include doAnimation(5s)
    }

    &-minute {
      height: $watchSize / 2.3;
      width: $minuteHandWidth;
      background-color: blue;
      left: $watchSize / 2 - $minuteHandWidth / 2;
      top: $watchSize / 2 - $watchSize / 2.3;
      @include doAnimation(60s)
    }
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
